export default {
    namespaced: true,
    state: {
        teacherTests: []
    },
    mutations: {
        SET_TEACHER_TESTS: (state, data) => {
            state.teacherTests=data
        }
    },
    actions: {
        async fetchTeacherTests({ commit }, id) {
            try {
                const response = await this.$axios.get(`/teacher/${id}/tests`)
                commit("SET_TEACHER_TESTS",response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        }
    }
}