export default {
    namespaced: true,
    state: {
        groupSchedule: []
    },
    mutations: {
        SET_GROUP_SCHEDULE: (state, data) => {
            for (var idx = 1; idx<=7; idx++){
                var item = {
                    id: 0,
                    scheduleDay: idx,
                    scheduleTime: 0
                }
                state.groupSchedule.push(item)
            }
            data.forEach(element => {
                state.groupSchedule[element.scheduleDay-1]=element
            });
        },
        ADD_DATA: (state, data) => {state.groupSchedule.push(data)},
        EDIT_DATA: (state, data) => {
            state.groupSchedule = state.groupSchedule.map(ele=>{
            if(ele.scheduleDay==data.scheduleDay){
                return data
            }
            else return ele
        })
    },
        REMOVE_DATA: (state, data) => {state.groupSchedule=state.groupSchedule.filter(ele=>ele.scheduleDay!=data.scheduleDay)},
        CLEAR_DATA: (state) => {state.groupSchedule=[]},
    },
    actions: {
        fetchGroupSchedule({ commit }, id) {
            this.$axios.get(`/admin/group/${id}/schedule`, id)
            .then(function (response) {
                commit("SET_GROUP_SCHEDULE",response.data);
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        }
    }
}