export default {
    namespaced: true,
    state: {
        teacherSchedule: []
    },
    mutations: {
        SET_TEACHER_SCHEDULE: (state, data) => {
            state.teacherSchedule=data
        }
    },
    actions: {
        async fetchTeacherSchedule({ commit }, id) {
            try {
                const response = await this.$axios.get(`/teacher/${id}/schedule`)
                commit("SET_TEACHER_SCHEDULE",response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        }
    }
}